.Lottery {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 80%;
  border: 2px solid rgb(138, 133, 133);
  margin: 30px 0;

  .Logo {
    margin: 30px 0;
  }

  .Balls {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .Generate-btn {
    background-color: rgb(40, 160, 29);
    color: white;
    border-radius: 5%;
    width: fit-content;
    margin: 30px 0;
    font-size: 20px;
    height: 50px;
    width: 260px;
  }
}
