$ball-size: 75px;

.LottoBall {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: tomato;
  border-radius: 50%;
  width: $ball-size;
  height: $ball-size;
  color: white;
  font-size: calc(#{$ball-size}/ 2);
  font-weight: bold;
  margin: 0 15px;
}
